/* Search by */
.ais-search-box--powered-by-link, .aa-powered-by-link {
  display: inline-block;
  width: 45px;
  height: 16px;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: middle;
}

/***************************/
/* autocomplete.js */
/***************************/

.clearfix {
  clear: both;
}

.algolia-autocomplete {
  width: 100%;
}

.aa-input {
  width: 100%;
  outline: none;
}

.aa-hint {
  width: 100%;
  color: #999;
}

.aa-dropdown-menu {
  width: 100%;
  margin-top: 4px;
  border: 1px solid #4C4C4C;
  border-radius: 5px;
  font-size: 14px;
  background-color: #fff;
}

.aa-article-hit--category {
  padding: 4px 5px;
  font-weight: bold;
  color: white;
}

.aa-article-hit .aa-article-hit--category {
  display: none;
}

.aa-article-hit.aa-article-hit__category-first .aa-article-hit--category {
  display: block;
}

.aa-article-hit--line {
  display: table;
  width: 100%;
}

.aa-article-hit--section {
  border-right: 1px solid #fff;
  border-top: 0px solid #fff;
  background: #F1F3F5;
  color: #555;
  display: table-cell;
  padding: 5px 7px 5px 5px;
  text-align: right;
  vertical-align: top;

  min-width: 100px; /* Hardcoded */
  max-width: 0px;
  width: 20%; /* Hardcoded */
}

.aa-article-hit__xs .aa-article-hit--section {
  display: none;
}

.aa-article-hit__sm .aa-article-hit--section {
  width: 30%; /* Hardcoded */
}

.aa-article-hit__section-first .aa-article-hit--section {
  border-top-width: 1px;
}

.aa-article-hit__category-first.aa-article-hit__section-first .aa-article-hit--section {
  border-top: none;
}

.aa-article-hit--section--inside {
  display: none;
  overflow: hidden;
  text-overflow: ellipsis;
}

.aa-article-hit__section-first .aa-article-hit--section--inside {
  display: block;
}

.aa-article-hit--content {
  display: table-cell;
  padding: 5px 10px;
  border-top: 1px solid #fff;
}

.aa-article-hit__xs .aa-article-hit--body {
  display: none;
}

.aa-article-hit__category-first.aa-article-hit__section-first .aa-article-hit--content {
  border-top: none;
}

.aa-article-hit--title {
  font-weight: bold;
}

.aa-powered-by {
  border-top: 1px solid #fff;
  padding: 6px 4px 0;
  text-align: right;
  font-size: .8em;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion {
  cursor: pointer;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion.aa-cursor {
  background-color: #efefef;
}



